<template>
  <div class="about">
    <van-popup v-model="show" round>
      <div class="top clearfix">
        <div class="img fl">
          <img :src="detailData.small_img" alt="" class="auto-img"/>
        </div>
        <div class="info fl">
          <div class="name">{{detailData.name}}</div>
          <div class="rules">
            <div class="enname">{{detailData.enname}}</div>
            <div class="like">
               <van-goods-action-icon
                  icon="like"
                  @click="toggleCollection"
                  :color="isCollection ? '#994a1b' : '#999999'"
                />
            </div>
          </div>
        </div>
      </div>
      <div class="content">
        <div class="item" v-for="(item,index) in detailData.rules" :key="index">
         {{item.title}}：
          <div class="rule_item">
            <div class="goods" v-for="(sub,i) in item.rule" :key="i"
             :class="item.ruleIndex == i ? 'active' : ''"
             @click="toggleState(item,i)">{{sub.title}}</div>
          </div>
        </div>
      </div>
      <div class="btn">
   

        <van-goods-action>
          <van-goods-action-button type="warning" text="加入购物袋" color="#994a1b"  @click="addProduct" />
          <van-goods-action-button type="danger" text="立即购买" color="#780d00"  @click="gobuy(true)" />
        </van-goods-action>
      </div>
      <div class="price">￥{{detailData.price}}</div>
    </van-popup>
 
  </div>
</template>

<script>
import "../assets/less/detailAbout.less";
export default {
  name: "DetailAbout",
  data() {
    return {
      // 商品pid
      pid: "",
      detailData: {},
      ruleIndex: 0,
      isCollection: false,
      product: {
        count: 1,
      },
      bagCount: 0,
      show:true,
      starRule:[],
    };
  },

  created() {
    this.pid = this.$route.params.pid;
    this.getDetailData();
    this.colletion();
    // this.shopBagCount();
  },

  methods: {
    //  获取商品详细数据
    getDetailData() {
      this.axios({
        method: "GET",
        url: "/productDetail",
        params: {
          appkey: this.appkey,
          pid: this.pid,
        },
      }).then((result) => {
        console.log(result);
          let data = result.data.result[0];
          data.desc = data.desc.split(/\n/);
          let arr = ["cream", "milk", "sugar", "tem"];
          let rules = [];

          arr.map((v) => {
            let obj = {};
            obj.title = data[v + "_desc"];
            //   被激活的下标
            obj.ruleIndex = 0;

            //   每项的规格
            obj.rule = [];
            let ruleString = data[v].split("/");
            ruleString.map((value) => {
              if (value != "") {
                obj.rule.push({ title: value });
              }
            });

            if (obj.rule.length > 0) {
              rules.push(obj);
            }
          });
          data.rules = rules;

          this.detailData = data;
        })
        .catch((err) => {
          console.log(err);
        });
    },

     // 切换商品规格
    toggleState(item, index) {
      if (item.ruleIndex == index) {
        return;
      }
      item.ruleIndex = index;
    },

     // 查询是否已经收藏商品
    colletion() {
      let tokenString = localStorage.getItem("tokenString");
      if (!tokenString) {
        this.$toast("您未登录账号");
        return;
      }
      // console.log(tokenString)
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "GET",
        url: "/findlike",
        params: {
          appkey: this.appkey,
          pid: this.pid,
          tokenString,
        },
      }).then((result) => {
        this.$toast.clear();
        if (result.data.code == 1000) {
          if (result.data.result.length > 0) {
            this.isCollection = true;
          }
        }

        this.$toast(result.data.msg);
      });
    },

    // 切换商品收藏与否
    toggleCollection() {
      let tokenString = localStorage.getItem("tokenString");
      if (!tokenString) {
        this.$toast("请先登录");
        return this.$router.push({ name: "Login" });
      }

      let url = this.isCollection ? "/notlike" : "/like";
      console.log(tokenString);
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "POST",
        url,
        data: {
          appkey: this.appkey,
          pid: this.pid,
          tokenString,
        },
      }).then((result) => {
        this.$toast.clear();

        if (result.data.code == 700) {
          this.$toast("请先登录!");
          this.$router.push({ name: "Login" });
        } else if (result.data.code == 800) {
          this.isCollection = true;
        } else if (result.data.code == 900) {
          this.isCollection = false;
        }

        this.$toast(result.data.msg);
      });
    },
     //  添加商品到购物袋中
    addProduct() {
      let tokenString = localStorage.getItem("tokenString");
      if (!tokenString) {
        this.$toast("请先登录");
        return this.$router.push({ name: "Login" });
      }
      let data = {
        tokenString,
        appkey: this.appkey,
        count: this.product.count,
        pid: this.pid,
      };

      let arr = [];
      // 遍历detailData.rules数组获取选择的商品规格
      this.detailData.rules.map((v) => {
        arr.push(v.rule[v.ruleIndex].title);
      });

      data.rule = arr.join("/");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "POST",
        url: "/addShopcart",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 700) {
            this.$toast("请先登录!");
            this.$router.push({ name: "Login" });
          } else if (result.data.code == 3000) {
            if (result.data.status == 1) {
              this.bagCount++;
            }
          }
          this.$toast(result.data.msg);
          this.show = false;
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
     // 立即购买
    gobuy(isBuy) {
      this.addProduct(isBuy);
      this.show = false;
    },
  
  },
};
</script>

<style lang="less" scoped>
</style>